@font-face {
  font-family: 'digital-italic';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}

body {
  --text-color: rgb(0, 133, 0);
  --background-color: rgba(0, 75, 0, 0.3);
  --hover-color: rgba(255, 255, 255, 0.37);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: rgb(44, 44, 44);
}

#header,
#footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#header {
  padding-top: 5px;
  padding-bottom: 5px;
  font-family: 'digital-italic';
  color: var(--text-color);
  font-size: 100px;
  letter-spacing: 10px;
  background-color: var(--background-color);
  border-bottom: solid var(--text-color) 0.75px;
}

#footer {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: auto;
  font-family: 'digital';
  color: var(--text-color);
  text-align: center;
  font-size: 32px;
  letter-spacing: 5px;
  background-color: var(--background-color);
  border-top: solid var(--text-color) 0.75px;
}

dialog {
  background-color: var(--background-color);
  border-radius: 10px;
  border: solid var(--text-color) 0.75px;
}

dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(12px);
}

input {
  font-size: 40px;
  font-family: 'digital-italic';
  color: var(--text-color);
  background-color: var(--background-color);
  border: solid var(--text-color) 0.75px;
  border-radius: 8px;
}

.dialog-button-container {
  display: flex;
  justify-content: center;
  gap: 5%;
}

#single-input-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
}

#pvp-dialog-container,
#pvc-dialog-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  font-size: 40px;
  font-family: 'digital-italic';
  color: var(--text-color);
}

#dual-input-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

#pvp-start-button {
  width: 100%;
}

#title {
  font-size: 100px;
  text-align: center;
}

.gameboard-ui-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  font-size: 40px;
  text-align: center;
  font-family: 'digital-italic';
  color: var(--text-color);
}

#gameboard-container {
  box-sizing: border-box;
  padding: 2% 5%;
  font-family: 'digital';
  color: var(--text-color);
  font-size: 40px;
  /* padding-left: 8%; */
  /* padding-right: 8%; */
  display: grid;
  justify-items: center;
  /* align-items: center; */
  grid-template-columns: auto 1fr auto;
  grid-template-rows: repeat(3, auto);
  gap: 26px;
}

#player1-title {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

#player2-title {
  grid-column: 3 / 4;
  grid-row: 1 / 2;
}

#gameboard-one {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
}

#gameboard-two {
  grid-column: 3 / 4;
  grid-row: 2 / 3;
}

#ui-container {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  font-family: 'digital';
  color: var(--text-color);
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1;
  grid-template-rows: 1fr 1fr auto;
  padding: 0 10%;
  /* padding-left: 5%;
  padding-right: 5%; */
}

#message-container,
#alert-container {
  border-radius: 10px;
  border: solid var(--text-color) 0.75px;
  background-color: var(--background-color);
  padding: 8px;
}

#message-container {
  text-align: left;
  font-size: 40px;
}

#alert-container {
  text-align: left;
  font-size: 48px;
}

#button-container {
  display: grid;
  place-items: center;
}

#player1-sunk-ships,
#player2-sunk-ships {
  width: 100%;
  box-sizing: border-box;
  padding-left: 50px;
  text-align: left;
}

#player1-sunk-ships {
  grid-column: 1 / 2;
  grid-row: 3 / 4;
}

#player2-sunk-ships {
  grid-column: 3 / 4;
  grid-row: 3 / 4;
}

#turn-button {
  font-size: 50px;
  width: 100%;
}

button {
  font-family: 'digital';
  color: var(--text-color);
  font-size: 40px;
  text-align: center;
  border-radius: 10px;
  border: solid var(--text-color) 0.75px;
  background-color: var(--background-color);
  padding: 8px;
}

button:hover {
  background-color: var(--hover-color);
}

.gameboard {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  gap: 2.5px;
}

.coordinate {
  height: 5vh;
  width: 5vh;
  border: solid var(--text-color) 0.75px;
  background-color: var(--background-color);
  display: grid;
  place-items: center;
  font-size: 30px;
  border-radius: 5px;
}

.active-coord:hover {
  background-color: var(--hover-color);
}

.ship {
  background-color: rgb(113, 197, 113);
}

.hit {
  background-color: rgb(255, 129, 129);
}

.miss {
  background-color: rgb(133, 133, 255);
}

#player1-sunk-ships,
#player2-sunk-ships {
  padding-left: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sunk-ship {
  box-sizing: border-box;
  padding: 5px;
  background-color: var(--background-color);
  border-radius: 10px;
  border: solid var(--text-color) 0.75px;
}
